"use client";

import { useEffect, useState } from "react";
import api from "@/lib/axios";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Users, CreditCard, ShoppingCart, Activity } from "lucide-react";

interface Order {
    id: number;
    reference: string;
    cost: number;
    user?: {
        email: string;
    };
}

interface AnalyticsStats {
    total_revenue: number;
    total_orders: number;
    pending_orders: number;
    total_users: number;
    recent_orders: Order[];
}

export default function AdminDashboardPage() {
    const [stats, setStats] = useState<AnalyticsStats | null>(null);

    useEffect(() => {
        api.get('/admin/analytics').then(res => setStats(res.data));
    }, []);

    if (!stats) return <div>Loading Analytics...</div>;

    const cards = [
        { title: "Total Revenue", value: `GHS ${stats.total_revenue}`, icon: CreditCard, color: "text-green-600" },
        { title: "Total Orders", value: stats.total_orders, icon: ShoppingCart, color: "text-blue-600" },
        { title: "Pending Orders", value: stats.pending_orders, icon: Activity, color: "text-orange-600" },
        { title: "Total Users", value: stats.total_users, icon: Users, color: "text-purple-600" },
    ];

    return (
        <div className="space-y-6">
            <h2 className="text-3xl font-bold tracking-tight">Admin Dashboard</h2>

            <div className="grid gap-4 md:grid-cols-4">
                {cards.map((card) => (
                    <Card key={card.title}>
                        <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                            <CardTitle className="text-sm font-medium text-muted-foreground">
                                {card.title}
                            </CardTitle>
                            <card.icon className={`h-4 w-4 ${card.color}`} />
                        </CardHeader>
                        <CardContent>
                            <div className="text-2xl font-bold">{card.value}</div>
                        </CardContent>
                    </Card>
                ))}
            </div>

            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-7">
                <Card className="col-span-4">
                    <CardHeader>
                        <CardTitle>Recent Orders</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="space-y-8">
                            {stats.recent_orders.map((order) => (
                                <div key={order.id} className="flex items-center">
                                    <div className="ml-4 space-y-1">
                                        <p className="text-sm font-medium leading-none">{order.reference}</p>
                                        <p className="text-sm text-muted-foreground">
                                            {order.user?.email || 'Unknown User'}
                                        </p>
                                    </div>
                                    <div className="ml-auto font-medium">
                                        +GHS {order.cost}
                                    </div>
                                </div>
                            ))}
                        </div>
                    </CardContent>
                </Card>
            </div>
        </div>
    );
}
